.baduk-board {
  position: relative;
  margin: 15px;
  background-image: url("./images/board.png");
  border: 0.25em solid rgb(202, 147, 58);
  color: rgb(94, 46, 12);
  margin-left: 8%; 
}

.intersection {
  background-size: 92%;
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  position: relative;
  z-index: 1;
  vertical-align: top;
  padding: 0;
  margin: 0; 
}

.intersection[data-color="B"] {
  background-image: url("./images/black.png");
}

.intersection[data-color="W"] {
  background-image: url("./images/white.png");
}

svg {
  position: absolute;
  display: block;
  z-index: 0;
  overflow: visible
}